header{
    background: #fff;
    display: flex;
    justify-content: space-between;
    padding: 1.1rem 1.5rem;
    box-shadow: 2px 2px 5px #f2f2f2;
    position: fixed;
    left: 21.563rem;
    width: calc(100% - 21.563rem);
    top: 0;
    z-index: 100;
    transition: left 300ms;
}
header h2{
    color: #222;
}
header label span{
    font-size: 1.7rem;
    padding-right: 1rem;
}
.search-wrapper {
    border: 1px solid #ccc;
    border-radius: 1.875rem;
    height: 3.125rem;
    display: flex;
    align-items: center;
    overflow-x: hidden;
}
.search-wrapper span{
    display: inline-block;
    padding: 0rem 1rem;
    font-size: 1.5rem;
}
.search-wrapper input{
    height: 100%;
    padding: .5rem;
    outline: none;
    border: none;
}

.user-wrapper {
    display: flex;
    align-items: center;
}
.user-wrapper img {
    border-radius: 50%;
    margin-right: 1rem;
}

.user-wrapper small {
    display: inline-block;
    color: var(--text-grey);
}
